import styled from 'styled-components';

const Container = styled.div`
  width: 300px;
  height: 200px;
  z-index: 999;
  
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  
  background-color: lightgray;
  border: 1px solid black;
  border-radius: 8px;
  
  button {
    :last-child {
      width: 100%;
      height: 30px;
      margin-top: 30px;
    }
  }
`

const Button = styled.button`
  position: absolute;
  right: 10px;
  top: 10px;
`
function RegisterModal({setModalOpen, clubName, name, phoneNumber}) {
    const closeModal = () => {
        setModalOpen(false);
    };

    return (
        <Container>
            <Button onClick={closeModal}>X</Button>
            <p> : {name}</p>
            <p> : {phoneNumber}</p>
            <p>{clubName}  ?</p>
            <button onClick={closeModal}></button>
        </Container>
    )
}

export default RegisterModal;