import './App.css';
import {Route, Routes} from "react-router-dom";
import ClubRegister from "./pages/clubRegister/clubRegister";

function App() {
  return (
    <Routes>
            <Route path="/" element={<ClubRegister clubName="" />} />
            <Route path="/clubinfo" element={<div>clubinfo</div>} />
            <Route path="/clublist" element={<div>clublist</div>} />
            <Route path="/clubregister" element={<ClubRegister clubName="Forif" />} />
            <Route path="/mypage" element={<div>mypage</div>} />
        </Routes>
  );
}

export default App;
