import React, {useState} from 'react';
import styles from './clubRegister.module.css'
import CheckBox from "./CheckBox";
import {PatternFormat} from "react-number-format";
import Logo_Remove from "./logos/Forif-removebg.png";
import RegisterModal from './Modal'
import Typewriter from 'typewriter-effect';
import styled from 'styled-components';
import "./Fonts/Font.css";

//Define Styles using styled-components
const Container = styled.div`
  padding: 0 60px;
  font-family: 'Gmarket Sans TTF Medium', serif;
`
const Title = styled.div`
  text-align: center;
  margin-bottom: 12px;

  div {
    //In Layout,  

    //In layout,  , {club_name}
    :last-child {
      //To display img and text in one line
      display: flex;
      align-items: center;
      justify-items: center;
      flex-direction: row;
      margin-top: 0.7rem;

      //To remove h1's default margin
      p {
        font-size: 4.87vw;
        margin: 0;
      }

      //Set img size to 40px
      img {
        width: 13vw;
        @media (max-width: 381px) {
          width: 10vw;
        }
      }
    }
  }
`

// Component
const Contour = styled.hr`
  height: 0;
  color: blue;
  margin-bottom: 24px;
`

//To explain why user should insert values.
const MutedText = styled.p`
  color: gray;
  font-size: 10px;
  margin-top: 3px;
  //Remove default margin
  margin-bottom: 0;
`
//In layout,    
const SubmitButton = styled.button`
  font-family: "Gmarket Sans TTF Light", sans-serif;
  color: white;
  font-weight: 600;
  width: 100%;
  //To set button_box size
  padding: 0.75rem 0.375rem;
  margin-top: 35px;
  font-size: 13px;
  line-height: 1.5;
  border-radius: 0.375rem;
  background-color: black;
  border-color: black;


  //When the button is disabled, set opacity to 0.2 and user cannot point on it.
  :disabled {
    pointer-events: none;
    opacity: 0.2;
  }
`
//To alert that the input value is empty
const AlertInputNone = styled.p`
  opacity: 0.8;
  color: red;
  margin-top: 3px;
  font-size: 10px;
`

//set props to get club_name
function ClubRegister(props) {
    //STATES
    const [isActive, setIsActive] = useState({
        inputName: false,
        inputPhone: false,
    });
    //Animation that the placeholder label goes up(and left)
    //When input is empty, the placeholder keeps down(and middle)
    const handleInputChange = (event) => {
        const {id, value} = event.target;
        setIsActive(prevState => ({...prevState, [id]: value !== ''}));
    }
    //Checkbox condition. When both service and Kakaotalk is checked,
    //enabled submit-button
    const [service, setService] = useState(false);
    const [KaKaoTalk, setKaKaoTalk] = useState(false);
    //get input.
    const [inputs, setInputs] = useState({
        name: '',
    });
    const [phoneNumber, setPhoneNumber] = useState();
    //Manage modal state.
    const [modalOpen, setModalOpen] = useState(false);

    //When you click the submit-button, show modal.
    const showModal = () => {
        setModalOpen(true);
    };
    //Get clubName props and convert it to local variable.
    const club_name = props.clubName;
    const {name} = inputs; //     
    //Check whether phone number is 3-4-4 form.
    const checkPhone = (phoneNumber) => {
        const phoneExp = /^\d{3}-\d{3,4}-\d{4}$/;
        return phoneExp.test(phoneNumber);
    }
    //Check whether all fields are filled.
    const allFieldChecked = ((name !== '') && (checkPhone(phoneNumber)) && service && KaKaoTalk);

    //onChange function. It allows input value to save its value.
    const onChangePhone = (values, e) => {
        setPhoneNumber(e.target.value);
        const {id, value} = e.target;
        setIsActive(prevState => ({...prevState, [id]: value !== ''}));
    }
    const onChange = (e) => {
        const {value, name} = e.target; //  e.target  name  value  
        setInputs({
            ...inputs, //  input   
            [name]: value // name    value  
        });
    };

    //PAGE LAYOUT
    return (
        //To set margin right and left.
        <Container>
            <Title>
                <div></div>
                <div>
                    <img src={Logo_Remove} alt={club_name}/>
                    <p>
                        <Typewriter
                            //Typing-like animation
                            //type ' ' -> Rest 0.5s -> '${club_name}'
                            onInit={(typewriter) => {
                                typewriter.typeString('<i> ,</i> ')
                                    .pauseFor(500)
                                    .typeString(`<strong><i>${club_name}</i></strong>`)
                                    .start();
                            }}
                            options={{
                                loop: false,
                                cursor: '',
                                //When the number of delay goes up, typed speed goes down
                                delay: 70,
                            }}
                        />
                    </p>
                </div>
            </Title>
            <Contour/>
            <div>
                <div className={styles.floatLabel}>
                    <input type="text" value={name || ''} name="name" onChange={(event) => {
                        onChange(event);
                        handleInputChange(event);
                    }} id="inputName"/>
                    <label className={isActive.inputName ? styles.Active : ""}>
                        <Typewriter
                            onInit={(typewriter) => {
                                typewriter.typeString(' !')
                                    .start();
                            }}
                            options={{
                                loop: false,
                                cursor: '',
                                delay: 70,
                            }}
                        />
                    </label>
                    <MutedText>
                        <Typewriter
                            onInit={(typewriter) => {
                                typewriter.typeString('  .')
                                    .start();
                            }}
                            options={{
                                loop: false,
                                cursor: '',
                                delay: 95,
                            }}
                        />
                    </MutedText>
                    <AlertInputNone>{name === '' && <Typewriter
                        onInit={(typewriter) => {
                            typewriter.typeString(' !')
                                .start();
                        }}
                        options={{
                            loop: false,
                            cursor: '',
                            delay: 40,
                        }}
                    />}</AlertInputNone>
                </div>
                <div className={styles.floatLabel}>
                    <PatternFormat
                        onValueChange={(values, sourceInfo) => {
                            onChangePhone(values.value, sourceInfo.event);
                            handleInputChange(sourceInfo.event);
                        }}
                        name="phone"
                        id="inputPhone"
                        value={phoneNumber} format="###-####-####"
                    />
                    <label className={isActive.inputPhone ? styles.Active : ""}>
                        <Typewriter
                            onInit={(typewriter) => {
                                typewriter.typeString('')
                                    .start();
                            }}
                            options={{
                                loop: false,
                                cursor: '',
                                delay: 80,
                            }}
                        />
                    </label>
                    <MutedText>
                        <Typewriter
                            onInit={(typewriter) => {
                                typewriter.typeString('       .')
                                    .start();
                            }}
                            options={{
                                loop: false,
                                cursor: '',
                                delay: 60,
                            }}
                        />
                    </MutedText>
                    <div>
                        {(checkPhone(phoneNumber) === false && phoneNumber !== '') &&
                            <AlertInputNone>
                                <Typewriter
                                    onInit={(typewriter) => {
                                        typewriter.typeString('   !')
                                            .start();
                                    }}
                                    options={{
                                        loop: false,
                                        cursor: '',
                                        delay: 40,
                                    }}
                                />
                            </AlertInputNone>}
                    </div>
                </div>
                <div>
                    <CheckBox checked={service} onChange={setService}>
                        <Typewriter
                            onInit={(typewriter) => {
                                typewriter.typeString('()   .')
                                    .start();
                            }}
                            options={{
                                loop: false,
                                cursor: '',
                                delay: 80,
                            }}
                        />
                    </CheckBox>
                    <CheckBox checked={KaKaoTalk} onChange={setKaKaoTalk}>
                        <Typewriter
                            onInit={(typewriter) => {
                                typewriter.typeString('()    .')
                                    .start();
                            }}
                            options={{
                                loop: false,
                                cursor: '',
                                delay: 70,
                            }}
                        />
                    </CheckBox>
                </div>
                <SubmitButton disabled={!allFieldChecked} onClick={showModal}>
                       
                </SubmitButton>
                {modalOpen && <RegisterModal setModalOpen={setModalOpen} title=" ?" name={name}
                                             phoneNumber={phoneNumber} clubName={club_name}/>}
            </div>
        </Container>
    );
}

export default ClubRegister;