import React from "react";
import styled from "styled-components"

const SwitchButton = styled.div `
  label {
    font-size: 0.76rem;
    display: flex;
    align-items: center;
    @media (max-width: 361px) {
      font-size: 0.68rem;
    }
  }
  
  input {
    appearance: none;
    position: relative;
    border: max(2px, 0.1rem) solid black;
    border-radius: 1.25rem;
    width: 1.7rem;
    height: 1rem;

    :enabled:hover {
      box-shadow: 0 0 0 max(3px, 0.1rem) lightgray;
    }

    ::before {
      content: "";
      position: absolute;
      left: 0;
      width: 0.8rem;
      height: 0.8rem;
      border-radius: 50%;
      transform: scale(0.8);
      background-color: black;
      transition: left 150ms linear;
    }

    :checked::before {
      background-color: white;
      left: 0.68rem;
    }

    :checked {
      background-color: black;
      border-color: black;
    }
    
    margin-left: auto;
  }
  
`
const CheckBox = ({ children,disabled,checked,onChange }) => {
    return (
        <SwitchButton>
            <label>
                {children}
                <input role="switch" type="checkbox" disabled={disabled} onChange={({target : {checked}}) => onChange(checked)} checked={checked} />
            </label>
        </SwitchButton>
    )
}
export default CheckBox;